<template>
  <div v-if="loaded">
    <div class="nav">
      <div class="logo">
        <span class="logo-text">云 SCEO</span>
      </div>
      <div class="head">
      </div>
      <div class="nav-top">
      </div>
    </div>
    <div class="main-area">
      <div class="nav-left">
        <el-menu default-active="2" :unique-opened="true">
          <el-submenu :index="itemLevel1.id" :key="itemLevel1.id" v-for="itemLevel1 in allMenus">
            <template slot="title">{{itemLevel1.name}}</template>
            <el-menu-item v-for="itemLevel2 in itemLevel1.childList" :key="itemLevel2.id" 
            :index="itemLevel2.id" @click="addTab(itemLevel2)">{{itemLevel2.name}}</el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
      <div class="main-content" ref="mainTab">
        <el-tabs type="border-card" class="tab" closable @tab-remove="removeTab" :active-name="activeId">
          <el-tab-pane v-for="item in currentTabs" :key="item.id" :name="item.id" :label="item.name">
            <iframe :src="item.href" frameborder="0" class="iframe" @load="fitFrame" scrolling="auto"></iframe>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>
<script>
  import { mapGetters } from 'vuex'
  import * as menuTypes from '../types/menu-types'

  export default {
    name: 'main',
    mounted () {
      this.$store.dispatch(menuTypes.RECIVE_MENUS).then(() => {
        this.loaded = true
      })
    },
    computed: {
      ...mapGetters({
        allMenus: menuTypes.GET_ALL_MENUS
      })
    },
    data () {
      return {
        loaded: false,
        showLang: false,
        activeId: null,
        currentTabs: []
      }
    },
    methods: {
      dispatch (path) {
        this.$router.push({name: path})
      },
      isCurrent (name) {
        if (this.matchedMenus) {
          let result = false
          this.matchedMenus.forEach((item, i) => {
            if (item.name === name) {
              result = true
              return true
            }
          })
          return result
        }
        return false
      },
      addTab (menu) {
        for (let index = 0; index < this.currentTabs.length; index++) {
          if (this.currentTabs[index].id === menu.id) {
            this.activeId = menu.id
            return
          }
        }
        const newTab = {
          id: menu.id,
          name: menu.name,
          href: menu.href
        }
        this.currentTabs.push(newTab)
        this.activeId = newTab.id
      },
      removeTab (id) {
        for (let index = 0; index < this.currentTabs.length; index++) {
          if (this.currentTabs[index].id === id) {
            this.currentTabs.splice(index, 1)
            break
          }
        }
      },
      fitFrame (event) {
        const iframe = event.target
        const tab = this.$refs.mainTab
        iframe.style.height = tab.offsetHeight - 42 + 'px'
      }
    }
  }
</script>
<style scoped>
  .nav{
    height: 80px;
    line-height: 80px;
    background: #4C5366;
    width: 100%;
  }
  .logo{
    background: url(../assets/images/main/logo.png) no-repeat 30px center;
    width: 180px;
    float: left;
  }
  .logo-text{
    height: 80px;
    line-height: 80px;
    font-size: 20px;
    user-select: none;
    cursor: default;
    color: #FFF;
    margin-left: 70px;
  }
  .head{
    width: 150px;
    float: left;
    background: #484F62;
    background: -webkit-gradient(linear, right 0, 0 0, from(#4C5366), to(#484F62));
    line-height: 60px;
    height: 80px;
  }
  .head-image{
    margin-left: 40px;
    height: 60px;
    width: 60px;
    border-radius: 30px;
    margin-top: 10px;
  }
  .head div{
    display: inline-block;
    float: right;
    line-height: 80px;
    margin-right: 20px;
  }
  .arrow{
    color: #FFF;
    margin-left: 10px;
    font-size: 10px;
    cursor: pointer;
  }
  .nav ul{
    text-align: right;
    height: 80px;
    width: 500px;
    margin: 0;
    padding: 0;
    float: right;
    user-select: none；
  }
  .nav li{
    list-style: none;
    float: left;
    cursor: pointer;
    height: 100%;
    width: 65px;
  }
  .nav li:hover{
    background-color: #383E4C;
  }
  .nav li div{
    height: 100%;
    width: 100%;
  }
  #home{
    background: url(../assets/images/main/icon-home.png) no-repeat center;
  }
  #center{
    background: url(../assets/images/main/icon-center.png) no-repeat center;
  }
  #message{
    background: url(../assets/images/main/icon-message.png) no-repeat center;
  }
  #help{
    background: url(../assets/images/main/icon-help.png) no-repeat center;
  }
  #lan{
    background: url(../assets/images/main/icon-zh.png) no-repeat center;
  }
  #theme{
    background: url(../assets/images/main/icon-theme.png) no-repeat center;
  }
  #exit{
    background: url(../assets/images/main/icon-exit.png) no-repeat center;
  }
  .main-area{
    position: absolute;
    top: 80px;
    bottom: 0px;
    width: 100%;
    padding: 0px;
  }
  .nav-left{
    top: 0px;
    bottom: 0px;
    width: 180px;
    position: absolute;
    background-color: #EEF1F6;
    z-index: 11;
  }
  .icon-home-mini{
    background: url(../assets/images/main/icon-home-mini.png) no-repeat 35px center;
  }
  .icon-my-account{
    background: url(../assets/images/main/icon-my-account.png) no-repeat 35px center;
  }
  .icon-my-subject{
    background: url(../assets/images/main/icon-my-subject.png) no-repeat 35px center;
  }
  .icon-my-job{
    background: url(../assets/images/main/icon-my-job.png) no-repeat 35px center;
  }
  .icon-my-SCEO{
    background: url(../assets/images/main/icon-my-SCEO.png) no-repeat 35px center;
  }
  .icon-setup{
    background: url(../assets/images/main/icon-setup.png) no-repeat 35px center;
  }
  .main-content{
    position: absolute;
    left: 180px;
    right: 0px;
    top: 0;
    bottom: 0;
    height: 100%;
  }
  .lang-item {
    height: 30px;
    line-height: 30px;
    font-size: 16px;
    cursor: pointer;
    padding-left: 15px;
  }
  .lang-item:hover{
    background: #eee;
  }
  .tab{
    height: 100%;
    border: none;
  }
  .iframe{
    margin: 0;
    width: 100%;
    overflow: auto;
  }
</style>



