import axios from 'axios'

export default {
  getMenus () {
    return new Promise((resolve, reject) => {
      axios.get('menu').then(data => {
        const sysMenu = data[0].childList
        sysMenu.forEach(item1 => {
          const baseHref = item1.href
          item1.childList.forEach(item2 => {
            item2.href = baseHref + item2.href
          })
        })
        resolve(sysMenu)
      }).catch(error => {
        reject(error)
      })
    })
  }
}



